export default function slider({data}) {
    return (
      <div className="slider">
        <div className="left"></div>
        <div className="right"></div>
        <div className="slider-wrap">
        {data.map((each,idx) => {
          <div key={'slide'+idx} className="slide" >
            {data[idx]}{idx}
          </div>
        })}
        </div>
        <style jsx>{`
        .slider {
          padding:36px;
        }
        @media only screen and (max-width: 960px)  {
        }
        `}</style>
      </div>
    )
}